@import "../../scss/mixin.scss";

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  z-index: 4;
  opacity: 0.5;
}

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 430px;
  z-index: 5;
  padding: 20px;
  box-sizing: border-box;
  max-height: 500px;
  overflow: hidden;
  box-shadow: 0px 3px 3px -2px rgba(0, 0, 0, 0.2),
    0px 3px 4px 0px rgba(0, 0, 0, 0.14), 0px 1px 8px 0px rgba(0, 0, 0, 0.12);

  @include mobile {
    width: 90%;
    height: 90%;
    padding: 20px;
  }
}
